.tile-filters {
    z-index: 1000;
    .filter-select {
        z-index: 1000;

        .city-dropdown {
            z-index: 1000;

            &:hover .options {
                display: block !important;
            }

            &.disabled .options {
                display: none !important;
            }
        }
    }
}

.tile-wrapper .tile-listing .tile {
    &.tile-type-image,
    &.tile-type-campaign,
    &.tile-type-selfie {
        .tile-canvas .tile-content .tile-centered-container .title-text-block {
            position: absolute;
            bottom: 10px;
        }
    }

    &.tile-type-cityconnect,
    &.tile-type-event,
    &.tile-type-text,
    &.tile-type-tweet {
        .tile-canvas .tile-content .tile-centered-container .title-text-block {
            padding-top: 40px;
        }
    }
}

.read-more {
    margin-top: 20px;
}
